.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  height: 100vh;
  display: flex;
}

#quote-box {
  display: grid;
  grid-template-columns: 1fr;
  border-radius: 10px;
  width: 50%;
  height: 60%;
  margin: auto auto;
  box-shadow: 10px 10px 5px 3px rgba(13, 6, 53, 0.566);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: #f0f8ff;
  grid-template-rows: auto 1fr auto auto;
}
#quote-box p {
  font-size: 1.4rem;
  line-height: 1.2em;
  margin: 20px;
  text-align: center;
}
#quote-box img {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 30%;
  height: 90%;
}
#author {
  font-size: 1.2rem;
  text-align: right;
  margin-right: 30px;
  font-style: italic;
  margin-bottom: 20px;
}
.action {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
#new-quote {
  font-size: 1.2rem;
  padding: 8px;
  border-radius: 5px;
  cursor: pointer;
}
#tweet-quote {
  font-size: 1.4rem;
  padding: 7px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 25px;
  text-decoration: none;
  color: white;
  background-color: #1da1f2;
}

@media (max-width: 768px) {
  #quote-box {
    width: 80%;
    height: auto;
  }
  #quote-box p {
    font-size: 1.2rem;
  }
  #author {
    font-size: 1rem;
  }
  #new-quote,
  #tweet-quote {
    font-size: 1rem;
    padding: 6px;
  }
}
